.form {
	max-width: 300px;
	min-width: 250px;
	position: relative;
	z-index: 2;
	&__content {
		padding-top: 40px;
		padding-bottom: 40px;
		padding-left: (32 / 300) * 100 * "1%";
		padding-right: (32 / 300) * 100 * "1%";
		border-radius: 10px;
		border: 5px solid #fff;
		background-color: transparent;
		text-align: center;
		font-size: 30px; }
	&__row {
		+ .form__row {
			margin-top: 14px; } }
	&__title {
		line-height: 1;
		margin-bottom: 32px; }
	&__btn {
		width: 100%;
		padding: 12px 16px;
		margin-top: 11px; } }
.input {
	display: block;
	width: 100%;
	padding: 13px 17px;
	border-radius: 10px;
	border: 2px solid #fff;
	background-color: transparent;
	@include bebas-reg;
	line-height: 1;
	@include fluid-font(22px, 24px, 900px, 1140px, 24px);
	color: #fff;
	@include placeholder-color(#fff); }
