.drink {
	position: relative;
	width: 50%;
	float: left;
	@include r(900) {
		float: none;
		width: 100%;
		margin-bottom: 20px; }
	&__top {
		@include evo;
		padding-right: 30px;
		@include r(900) {
			padding-right: 0;
			text-align: center; } }
	&__top-small {
		color: #303030;
		font-size: 24px;
		@include fluid-font(20px, 24px, 900px, 1170px, 24px);
		line-height: (61 / 24);
		letter-spacing: -0.01em;
		@include r(900) {
			font-size: 30px;
			line-height: 1.5; }
		@include r(500) {
			font-size: 26px; } }
	&__top-big {
		@include fluid-font(74px, 90px, 900px, 1170px, 90px);
		color: #ff7200;
		line-height: (103 / 90);
		letter-spacing: -0.01em;
		@include r(900) {
			font-size: 60px; }
		@include r(500) {
			font-size: 36px; } } }
