.section1 {
	background: url(../img/section1/bg-sec1.jpg) no-repeat center;
	overflow: hidden;
	.inner {
		padding-top: 44px;
		padding-bottom: 194px;
		z-index: 1;
		position: relative;
		@include r(900) {
			padding: 74px 15px 194px; }
		@include r(500) {
			padding: 70px 15px; }
		&:before {
			@include rmin(901) {
				content: '';
				display: block;
				position: absolute;
				width: ((723 / 1170) * 100) * "1%";
				height: ((488 / 1170) * 100) * "1%";
				left: ((109 / 1170) * 100) * "1%"; // 109px
				bottom: ((-20 / 1170) * 100) * "1%";
				background: url(../img/section1/product-main__shadow.png) no-repeat center;
				opacity: 0.49;
				z-index: -1; } } } }
.prices {
	@include hide-text;
	display: inline-block;
	vertical-align: bottom;
	width: (104 / 215) * 100 * "1%";
	bottom: -20px;
	position: relative; }
.desc {
	@include bebas-bold;
	@include fluid-font(24px, 30px, 900px, 1140px, 30px);
	line-height: 1;
	max-width: (94 / 215) * 100 * "1%";
	display: inline-block;
	vertical-align: bottom;
	margin: 0 0 12px 10px; }
.content {
	@include rmin(901) {
		width: ((475 / 1140) * 100) * "1%";
		float: right; }
	&__top {
		@include r(900) {
			margin-bottom: 70px; } }
	&__body {
		@include rmin(901) {
			max-width: 420px;
			padding-left: 15px; }
		@include r(900) {
			max-width: 345px;
			float: right;
			.btn-primary {
				display: none; } }
		@include r(500) {
			max-width: none;
			float: none; } } }
.section2 {
	background: url(../img/section2/bg-sec2.png) no-repeat center top;
	overflow: hidden;
	margin-top: -86px;
	z-index: 10;
	@include r(1100) {
		margin-top: -100px; }
	@include r(900) {
		background: #fff;
		margin-top: 0;
		overflow: visible;
		&:before,
		&:after {
			content: '';
			display: block;
			position: absolute;
			z-index: 1;
			bottom: 100%;
			height: 80px;
			width: 50%;
			background-color: #FFF; }
		&:before {
			left: -50%;
			transform: skewX(85deg); }
		&:after {
			right: -50%;
			transform: skewX(-85deg); } }
	.inner {
		padding-top: 140px;
		padding-bottom: 140px;
		z-index: 2;
		@include clr;
		@include r(900) {
			padding-top: 60px;
			padding-bottom: 60px; } } }
.section3 {
	background: url(../img/section3/bg-sec3.png) no-repeat center top;
	overflow: hidden;
	margin-top: -120px;
	.inner {
		padding-top: 29px;
		padding-bottom: 113px; } }
.section4 {
	background-color: #FFF;
	margin-top: -14px;
	.inner {
		padding-top: 119px;
		padding-bottom: 92px;
		z-index: 2; } }
.section5 {
	margin-top: -235px;
	background: url(../img/section5/bg-sec5.png) no-repeat center bottom;
	overflow: hidden;
	z-index: 1;
	.inner {
		padding-top: 277px;
		padding-bottom: 134px; } }
.section6 {
	margin-top: -91px;
	background: url(../img/section6/bg-sec6.jpg) no-repeat center;
	overflow: hidden;
	.inner {
		z-index: 1;
		padding-top: 166px;
		padding-bottom: 292px; } }
.section {
	position: relative;
	background-size: cover; }
