.counter {
	font-size: 0;
	margin-bottom: 40px;
	white-space: nowrap;
	text-align: center;
	@include r(500) {
		white-space: normal;
		margin-bottom: 0; }
	&__col {
		display: inline-block;
		vertical-align: middle; }
	&__col1 {
		width: 55%;
		@include r(500) {
			width: 100%; } }
	&__col2 {
		text-align: right;
		width: 45%;
		@include r(500) {
			width: 100%;
			text-align: center;
			margin-top: 30px; } }
	&__pointer {
		background-color: #FF7200;
		position: relative;
		@include rmin(901) {
			padding: 0.666em 0.5em 0.666em 0;   // 16px, 12px
			width: ((154 / 24) * "1em");  // (207 - 12 - 41)
			margin-right: ((52 / 24) * "1em");
			margin-left: (12 / 24) * "1em";
			@include fluid-font(20px, 24px, 900px, 1170px, 24px); }
		@include r(900) {
			font-size: 30px;
			margin-left: 12px;
			margin-right: 52px;
			padding: 12px 0; }
		@include r(500) {
			margin: 0; }
		&:before {
			background: url(../img/section1/pointer1.png) no-repeat center;
			left: -(12 / 24) * "1em";
			width: (12 / 24) * "1em";
			@include r(900) {
				left: -12px;
				width: 12px; }
			@include r(500) {
				display: none; } }
		&:after {
			left: 100%;
			margin-left: -2px;
			background: url(../img/section1/pointer2.png) no-repeat center;
			width: (41 / 24) * "1em";
			@include r(900) {
				width: 41px; }
			@include r(500) {
				display: none; } }
		&:after,
		&:before {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			bottom: 0;
			height: (65 / 24) * "1em";
			margin: auto;
			background-size: contain;
			@include r(900) {
				height: 65px; } }
		span {
			position: relative;
			z-index: 1;
			@include rmin(901) {
				margin-right: -1em; } } } } // (24 / 24)
.counter-list {
	&__item {
		color: #ff7200;
		@include evo;
		@include fluid-font(36px, 48px, 900px, 1170px, 48px);
		display: inline-block;
		vertical-align: top;
		padding: 0 12px;
		line-height: (79 / 48);
		background: url(../img/count.png) no-repeat center;
		background-size: contain;
		@include r(900) {
			font-size: 40px; }
		+ .counter-list__item {
			margin-left: (5 / 182) * 100 * "1%";
			@include r(900) {
				margin-left: 2px; } }
		@include r(500) {
			font-size: 42px; } } }
