.present {
	@include clr;
	&__title {
		color: #fff;
		max-width: 905px;
		margin: 0 auto 70px;
		text-align: center;
		@include evo;
		line-height: 1;
		@include fluid-font(38px, 48px, 900px, 1140px, 48px);
		span {
			@include fluid-font(56px, 72px, 900px, 1140px, 72px);
			margin-top: 24px;
			display: block; } }
	&__col1 {
		width: 45%;
		float: left;
		padding-left: (67 / 1140) * 100 * "1%";
		padding-top: 46px;
		.advantage {
			margin: 0; }
		.counter {
			margin: 46px 0 0; } }
	&__col1-inner {
		max-width: 390px; }
	&__col2 {
		width: 25%;
		// padding-right: 50px
		float: left;
		position: relative; }
	&__col3 {
		overflow: hidden;
		position: relative; } }
